import React, { Suspense, lazy } from "react";
import {
  HashRouter as Router,
  Redirect,
  Switch,
  Route
} from "react-router-dom";

import { CircularProgress } from "@material-ui/core";

const PageNotFound = lazy(() => import("./PageNotFound.jsx"));
const Header = lazy(() => import("./common/Header.jsx"));

const ListOfUsers = lazy(() => import("./ListOfUsers.jsx"));
const AddNewUser = lazy(() => import("./AddNewUser.jsx"));
const UserView = lazy(() => import("./UserView.jsx"));
const Editing = lazy(() => import("./Editing.jsx"));

const App = () => {
  return (
    <Router>
      <Suspense fallback={<CircularProgress />}>
        <Header />
        <Switch>
          <Route
            path="/"
            exact
            render={() => <Redirect to="/registration?tab=account" />}
          />
          <Route path="/registration" exact component={AddNewUser} />
          <Route path="/users" exact component={ListOfUsers} />
          <Route path="/users/edit/:id" exact component={Editing} />
          <Route path="/users/view/:id" exact component={UserView} />
          <Route component={PageNotFound} />
        </Switch>
      </Suspense>
    </Router>
  );
};

export default App;
